*{
  margin: 0;
  padding: 0;

  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  scroll-behavior: smooth;
}





html,body{
  width: 100vw;
  height: 100vh;

  

}


#root{
  width: 100%;
  height: 100%;
}


@font-face {
  font-family: "Caviar Dreams";
  src: local(CaviarDreams), url("./components/files/fonts/CaviarDreams.ttf");
}

@font-face {
  font-family: "Rocket";
  src: local(CaviarDreams), url("./components/files/fonts/Rocket\ Regular.otf");
}

@font-face {
  font-family: "Caviar Dreams";
  src: local(CaviarDreams),
    url("./components/files/fonts/CaviarDreams_Bold.ttf");
  font-weight: bold;
}

@font-face {font-family: "MuseoSansRounded-5";
  src: url("./components/files/fonts/a07bcad627607b6777616f8e058e8917.eot"); /* IE9*/
  src: url("./components/files/fonts/a07bcad627607b6777616f8e058e8917.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
  url("./components/files/fonts/a07bcad627607b6777616f8e058e8917.woff2") format("woff2"), /* chrome、firefox */
  url("./components/files/fonts/a07bcad627607b6777616f8e058e8917.woff") format("woff"), /* chrome、firefox */
  url("./components/files/fonts/a07bcad627607b6777616f8e058e8917.ttf") format("truetype"), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
  url("./components/files/fonts/a07bcad627607b6777616f8e058e8917.svg#MuseoSansRounded-500") format("svg"); /* iOS 4.1- */
}