.Footer{
    width: 100%;
    margin-top: 30px;

    display: flex;
    flex-direction: column;
    align-items: center;
    /* background-color: red; */
}


.FooterLine{
    width: 80%;
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, transparent, #ccc, transparent);
}


.RedesSociais{
    margin: 20px 0;
    list-style-type: none;
    display: flex;
    justify-content: center;
    width: 100%;
}

.RedesSociais li{
    margin: 5px 10px;
    font-size: 2rem;

    cursor: pointer;
}

.RedesSociais li a{
    color: white;
    text-decoration: none;
}