.Skills{
    width: 100%;
    padding: 30px 0;
    display: flex;
    
}



.SkillsList{
    flex: 1;

    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;

}
.TitleSkills{
    font-size: 3rem;
    font-family: "Caviar Dreams";
    margin: 20px 0;

    color: white;
}

.Javascript{
    font-size: 6.5rem;
    color: rgb(255, 234, 0);
}

.React{
    font-size: 5.5rem;
    color: aqua;
}


.SkillsList ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    width: 550px;
    list-style-type: none;

}

.SkillsList ul li{
    width: 120px;
    height: 120px;
    background-color: #1818187d;

    border-radius: 15px;
    margin: 20px;

    display: flex;
    justify-content: center;
    align-items: center;

}

.SkillsList ul li img{
    width: 70%;
}





.SkillsExperience{
    flex: 1;
    max-width: 50%;

    display: flex;
    justify-content: center;
}

@media screen and (max-width:1100px){
    .SkillsList ul li{
        width: 100px;
        height: 100px;
    }


    .SkillsList ul{
        width: 100%;
    }

    .Javascript{
        font-size: 5.7rem;
    }
}


@media screen and (max-width:950px){
    .Skills{
        flex-direction: column;
    }

    .SkillsList ul{
        width: 500px;
    }

    .SkillsExperience{
        max-width: 100%;
        width: 100%;

        margin: 20px 0;
    }
}

@media screen and (max-width:450px){
    .SkillsList ul li{
        width: 90px;
        height: 90px;
    }
    .Javascript{
        font-size: 5rem;
    }
}