.Intro {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;

  background-color: #070707;
  display: flex;
  justify-content: center;
  align-items: center;

  animation: OutIntro 0.5s 10.5s linear forwards;

}

.NameAnimation {
  display: flex;
  flex-direction: column;

  width: 380px;

  animation: Blur 0.5s linear forwards;
}

.Name {
  display: flex;
  justify-content: center;
  width: 100%;

  font-size: 5rem;
  font-family: "Rocket";
  color: white;
}

.ListHabilities {
  position: relative;
  width: 100%;
  height: 40px;
  /* background-color: yellow; */

  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ListHabilities li {
  position: absolute;
  font-size: 1.3rem;
  font-family: "caviar dreams";
  font-weight: bold;
  color: white;
  opacity: 0;

  text-transform: uppercase;

  animation: Passarela 1.5s linear forwards;
}

@keyframes Passarela {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes Blur {
  0% {
    filter: blur(30px);
  }
  100% {
    filter: blur(0px);
  }
}


@keyframes OutIntro {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    pointer-events: none;

  }
}


