.BiographyPerf{
    width: 100%;

    display: flex;

    padding: 70px 0;
}

.BiographyPerfPhoto{
    max-width: 35%;
    flex: 1;


    display: flex;
    justify-content: flex-end;
    padding: 20px 20px;

}


.BiographyPerfContent{
    flex: 1;
    padding: 10px 0;


}

.PhotoPerf{
    position: relative;
    min-width: 300px;
    min-height: 300px;

    border-radius: 100%;

    background-image: url('../../files/img/Image.jpeg');
    background-position: center;
    background-size: cover;

    outline: 5px solid #363636;

    box-shadow: 0 0 70px -5px #69696984;

}

.PhotoPerfDetail{
    width: 280px;
    height: 280px;

    border-radius: 100%;
    opacity: 0.3;
    transform: translateY(70%) translateX(-50%);

    background-image: url('../../files/img/details.png');
    background-position: center;
    background-size: cover;
}

.BiographyPerfTitle{
    font-size: 3rem;
    font-family: "Caviar Dreams";
    margin: 0 0 20px 0;

    color: white;    
}

.BiographyPerfText{

    max-width: 600px;
    

    text-align: justify;
    font-size: 1.1rem;
    font-family: "Caviar Dreams";
    margin: 5px 0;
    line-height: 1.5;

    color: white;
}



@media screen and (max-width: 900px){
    .BiographyPerf{
        flex-direction: column;
        align-items: center;
    }

    .BiographyPerfText, .BiographyPerfTitle{
        padding: 0 15px;
    }

    .PhotoPerfDetail{
        transform: translateY(40%) translateX(50%);
    }

    .BiographyPerfPhoto{
        max-width: 100%;
        margin: 10px 0;
    }
}