.ContainerCarrossel{
    width: 90%;
    height: 100%;

    display: flex;
    align-items: center;


}


.itemCarrossel{
    background-color: #1818187d;
    padding: 10px 20px;

    border-radius: 10px;
}

.TitleCarrossel{
    font-size: 2rem;
    font-family: "Caviar Dreams";
    margin: 20px 0;

    color: white;
}

.TextCarrossel{
    font-size: 1.2rem;
    font-family: "Caviar Dreams";
    margin: 20px 0;
    color: white;


}

@media screen and (max-width:500px){
    .ContainerCarrossel{
        width: 100%;
    }
}