.Formation{
    position: relative;
    width: 100%;
    padding: 80px 20px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.TitleFormation{
    z-index: 2;
    font-size: 3rem;
    font-family: "Caviar Dreams";
    margin: 20px 0;

    color: white;
}

.ContentFormationItem{
    z-index: 2;
    max-width: 600px;
    background-color: #181818;
    margin: 20px 0;
    padding: 20px 10px;
    border-radius: 10px;
}

.ContentFormationTitle{
    font-size: 2rem;
    font-family: "Caviar Dreams";
    margin: 5px 0;

    color: white;
}

.DateFormation{
    font-size: 0.9rem;
    font-family: "Caviar Dreams";
    margin: 5px 0;

    color: rgba(255, 255, 255, 0.658);
}

.InstFormation{
    font-size: 1.1rem;
    font-family: "Caviar Dreams";
    margin: 5px 0;

    color: white;
}

.Details{
    z-index: 1;

    position: absolute;
    width: 400px;
    height: 400px;

    bottom: 0;
    right: -130px;
    opacity: 0.1;

    border-radius: 100%;

    background-position: center;
    background-size: cover;

    background-image: url('../../files/img/details.png');
}