.Works{
    width: 100%;
    min-height: 500px;

    margin: 100px 0;

    display: flex;
    flex-direction: column;
    align-items: center;  

}


.WorksTitle{
    font-size: 3.3rem;
    font-family: "Caviar Dreams";
    margin: 10px 0 30px;

    color: white;
}


.WorksContainer{
    width: 85%;


    display: flex;
    flex-wrap: wrap;

    justify-content: center;
    gap: 40px;


}


.WorksItem{
    width: 330px;
    height: 450px;

    background-color: #181818;
    border-radius: 10px;

    display: flex;
    flex-direction: column;

    cursor: pointer;

    transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    -moz-transition: .3s ease-in-out;
    -webkit-transition: .3s ease-in-out;

}

.WorksItem:hover{
    scale: 1.15;
}



.imageWorkItem{
    flex: 1;
    max-height: 60%;
    min-height: 60%;

    display: flex;
    justify-content: center;
    align-items: center;


}

.imageWorkItem img{
    width: 75%;
    border-radius: 5px;
}

.descWorkItem{
    flex: 1;
    background-color: rgba(0, 0, 0, 0.188);

    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.TitleWorkItem{
    font-size: 2rem;
    font-family: "Caviar Dreams";
    margin: 5px 15px;

    color: white;
}

.DescWorkItem{
    font-size: 1.03rem;
    font-family: "Caviar Dreams";
    padding: 5px 15px;

    color: white;

    
}





